*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.content {
  height: 80vh;
}

.resgister-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.navbar{
  background-color: #003366  !important;
  border-radius: 10px !important;
  margin-top: 2px !important;
  color: white !important;
  
}
.navbar-brand  {
  color : white !important;
  font-size: 25px !important;
}

.nav-link{
  color: white !important;
  font-size: 18px;
  padding-top: 8px ;
  margin-right: 9px;
}
body{
  background-color: #ffff !important;
}


/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  margin-bottom: 10px;
}
/* transection page */

/* antd icons */

.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}
.inactive-icon {
  color: gray;
}

.filter-tab .ant-select-selector {
  width: 150px !important;
}