.login-page {
  height: 120vh;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.login-form {
  background-color: #b9bcccb6;
  margin-left: -15px;
  padding: 30px;
}

.login-form h1 {
  text-align: center;
  font-weight: bold;
  font-size: 20px !important;
  margin: 30px;
  color: #344265;
}

.login-form .ant-form-item-label label {
  color: #344265 !important;
}
.login-form button {
  background-color: #344265;
  color: white;
  outline: none;
}
.login-form button:hover {
  background-color: black;
  color: white;
}
.login-form a {
  color: #344265 !important;
}

.login-form a:hover {
  color: black !important;
}

.custom-form-item {
  margin-bottom: 20px; /* Adjust spacing */
}

.custom-form-item label {
  font-weight: bold; /* Customize label */
  color: #292828;
}

.custom-input {
  border: 2px solid #439af6; /* Custom border color */
  border-radius: 5px; /* Rounded corners */
}

.custom-input:hover {
  border-color: #0056b3; /* Hover effect */
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.5); /* Glow effect */
}
