.register-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to right, #f0c27b, #4b1248);
  padding: 2rem;
}
.form-container h1{
  color: white;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-decoration: underline;
}

.register-container {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  width: 90%;
  max-width: 1200px;
  gap: 3rem;
  margin: auto;
}

.register-form {
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  padding: 2.5rem;
  width: 35%;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease;
}

.register-form:hover {
  transform: translateY(-5px);
}

.image-container {
  width: 55%;
  display: flex;
  justify-content: center;
}


.register-image {
  width: 90%;
  height: auto;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

.register-form h2 {
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 700;
  color: #966527;
  font-size: 1.75rem;
}

.register-form input {
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
}

.register-form input:focus {
  border-color: #966527 !important;
  box-shadow: 0 0 0 2px rgba(184, 115, 51, 0.2);
  outline: none;
}

.register-form a {
  color: #966527 !important;
  text-decoration: none;
  transition: color 0.3s ease;
}

.register-form a:hover {
  color: #734d1f !important;
  text-decoration: underline;
}

.register-form button {
  width: 100%;
  padding: 0.75rem;
  background-color: #966527;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.register-form button:hover {
  background-color: #734d1f;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .register-container {
    flex-direction: column;
    gap: 2rem;
  }
  
  .register-form {
    width: 100%;
    padding: 1.5rem;
  }
  
  .image-container {
    width: 100%;
    order: -1;
  }
}



.custom-form-item {
  margin-bottom: 20px; /* Adjust spacing */
}

.custom-form-item label {
  font-weight: bold; /* Customize label */
  color: #292828;
}

.custom-input {
  border: 2px solid #439af6; /* Custom border color */
  border-radius: 5px; /* Rounded corners */
}

.custom-input:hover {
  border-color: #0056b3; /* Hover effect */
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.5); /* Glow effect */
}
